<script>
import {
    BCard,
    BCardText,
    BTableLite,
    BCol,
    BContainer,
    BRow,
    BSkeleton,
    BFormGroup,
    BFormInput,
    BInputGroup,
    BForm,
    BButton,
    BModal,
} from "bootstrap-vue";
import axios from "@/libs/axios";
import PeopleIcon from "@/@core/components/svg-icons/PeopleIcon.vue";
import MoneyIcon from "@/@core/components/svg-icons/MoneyIcon.vue";
import ListIcon from "@/@core/components/svg-icons/ListIcon.vue";
import BoardIcon from "@/@core/components/svg-icons/BoardIcon.vue";
import { generateBgForBox } from "@/@core/utils";
import { separator } from "@core/helper";

export default {
    components: {
        BCard,
        BCardText,
        BTableLite,
        BCol,
        BContainer,
        BRow,
        PeopleIcon,
        MoneyIcon,
        ListIcon,
        BoardIcon,
        BSkeleton,
        BFormGroup,
        BFormInput,
        BInputGroup,
        BForm,
        BButton,
        BModal,
    },
    data() {
        return {
            loading: false,
            pageData: [],
            debtorData: [
                {
                    name: " ",
                    floor: " ",
                    unit: "102",
                    price: "122333",
                },
                {
                    name: " ",
                    floor: " ",
                    unit: "102",
                    price: "122888",
                },
                {
                    name: " ",
                    floor: " ",
                    unit: "102",
                    price: "786333",
                },
            ],
            token: '',
            newBalance: '',
        }
    },
    created() {
        this.getDashboard()
    },
    methods: {
        separate() {
            this.newBalance = separator(this.newBalance)
        },
        goToList(userId) {
            this.$router.push('/debtor?id=' + userId)
        },
        async getDashboard() {
            try {
                this.token = localStorage.getItem('token')
                this.loading = true;
                const { data } = await axios.get(
                    "/manager/dashboard",
                    {
                        headers: {
                            'Authorization': 'Bearer ' + this.token,
                        },
                    }
                );
                this.pageData = data;
                this.loading = false;
                // console.log(data);
            } catch (err) {
                console.log(err);
                this.$router.push({ path: '/login' })
            }
        },
        balanceEdit() {
            this.$bvModal.hide('new-balance')
            axios
                .post('//sm.oop.ir/api/v1/manager/dashboard/set-ready',
                    {
                        initial_balance: this.newBalance
                    },
                    {
                        headers: {
                            'Authorization': 'Bearer ' + this.token
                        }
                    })
                .then(response => {
                    this.getDashboard()
                })
        },
        setBalance() {
            this.newBalance = separator(this.pageData.initial_balance)
        },
        generateBgForBox,
    },
};
</script>

